import Vue from 'vue'
import Router from 'vue-router'
import Pvw from '../views/Pvw.vue'
import Pgm from '../views/Pgm.vue'

Vue.use(Router)

export default new Router({
  base: '/scene/',
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component: Pgm
    },
    {
      path: '/pvw',
      name: 'Pvw',
      component: Pvw
    },
    {
      path: '/pgm',
      name: 'Pgm',
      component: Pgm
    }
  ]
})
