<template>
  <div
    class="bar-ele"
    ref="bar"
    :class="{ selected: selected }"
    draggable="false"
    v-if="isShow == 1"
    :src="data.src"
    :style="styleBasic"
    :tabindex="1"
  >
    <img
      v-if="data.style <= 3"
      class="m-img"
      draggable="false"
      :src="
        'https://static.guangdianyun.tv/static/broadCloud/console/newBcc/lcpsMake/img/bar' +
          data.style +
          '.png'
      "
    />
    <img
      v-if="data.style >= 4 && $store.state.size.name == '720p'"
      class="m-img"
      draggable="false"
      :src="
        'https://guangdianyun.oss-cn-hangzhou.aliyuncs.com/lcpsSource/bar/' +
          data.style +
          '.png'
      "
    />
    <img
      v-if="data.style >= 4 && $store.state.size.name == '1080p'"
      class="m-img"
      draggable="false"
      :src="
        'https://guangdianyun.oss-cn-hangzhou.aliyuncs.com/lcpsSource/bar/' +
          data.style +
          '_h.png'
      "
    />
    <div
      v-if="$store.state.size.name == '720p'"
      :class="[
        {
          'bar-font-1': Number(data.style) == 1,
          'bar-font-2': Number(data.style) == 2,
          'bar-font-3': Number(data.style) == 3,
          'bar-font-4': Number(data.style) == 4,
          'bar-font-5': Number(data.style) == 5,
          'bar-font-6': Number(data.style) == 6,
          'bar-font-7': Number(data.style) == 7,
          'bar-font-8': Number(data.style) == 8,
          'bar-font-9': Number(data.style) == 9,
          'bar-font-10': Number(data.style) == 10
        }
      ]"
    >
      <div class="bar-text-main" :style="styleFont">{{ data.maintitle }}</div>
      <div class="bar-text-sub" :style="styleFont">{{ data.subtitle }}</div>
    </div>
    <div
      v-if="$store.state.size.name == '1080p'"
      :class="[
        {
          'bar-font-1-high': Number(data.style) == 1,
          'bar-font-2-high': Number(data.style) == 2,
          'bar-font-3-high': Number(data.style) == 3,
          'bar-font-4-high': Number(data.style) == 4,
          'bar-font-5-high': Number(data.style) == 5,
          'bar-font-6-high': Number(data.style) == 6,
          'bar-font-7-high': Number(data.style) == 7,
          'bar-font-8-high': Number(data.style) == 8,
          'bar-font-9-high': Number(data.style) == 9,
          'bar-font-10-high': Number(data.style) == 10
        }
      ]"
    >
      <div class="bar-text-main" :style="styleFont">{{ data.maintitle }}</div>
      <div class="bar-text-sub" :style="styleFont">{{ data.subtitle }}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'BarEle',
  props: {
    isShow: Number,
    data: Object,
    updateTime: Number,
    selected: Boolean,
    removable: {
      type: Boolean,
      default: false
    },
    allowedOverStep: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      width: 0,
      height: 0,
      startX: 0,
      startY: 0,
      startTop: 0,
      startLeft: 0
    }
  },
  computed: {
    styleBasic() {
      return {
        top: this.data.top + '%',
        left: this.data.left + '%',
        opacity: this.data.opacity / 100,
        zoom: this.data.zoom + '%',
        zIndex: this.updateTime
      }
    },
    styleAnime() {
      return {}
    },
    styleFont() {
      return {
        fontSize: this.data.size + 'px',
        // fontWeight: "bold",
        color: this.data.color
      }
    }
  },
  methods: {
    mouseDown(event) {
      let coefficient = 0.3125
      if (this.$store.state.size.name === '1080p') coefficient = 0.20833333
      if (this.removable) {
        this.$nextTick(() => {
          this.width = this.$refs.bar.clientWidth
          this.height = this.$refs.bar.clientHeight
        })
        this.startY = event.clientY
        this.startX = event.clientX
        this.startLeft = Math.round(
          (Number(this.data.left) * this.$store.state.size.w) / 100
        )
        this.startTop = Math.round(
          (Number(this.data.top) * this.$store.state.size.h) / 100
        )
        console.log(this.startX, this.startY, this.startTop, this.startLeft)

        const move = moveEvent => {
          const currX = moveEvent.clientX
          const currY = moveEvent.clientY
          if (this.allowedOverStep) {
            this.data.top = Math.round(
              ((currY - this.startY + this.startTop) * 100) /
                this.$store.state.size.h
            )
            this.data.left = Math.round(
              ((currX - this.startX + this.startLeft) * 100) /
                this.$store.state.size.w
            )
          } else {
            //  高
            const top = Math.round(
              (((currY - this.startY) / coefficient + this.startTop) * 100) /
                this.$store.state.size.h
            )
            if (top < 0) {
              this.data.top = 0
            } else if (
              Math.round(
                (top * this.$store.state.size.h) / 100 +
                  Math.round((this.height * this.data.zoom) / 100)
              ) > this.$store.state.size.h
            ) {
              this.data.top =
                ((this.$store.state.size.h -
                  Math.round((this.height * this.data.zoom) / 100)) *
                  100) /
                this.$store.state.size.h
            } else {
              this.data.top = top
            }
            //  宽
            const left = Math.round(
              (((currX - this.startX) / coefficient + this.startLeft) * 100) /
                this.$store.state.size.w
            )
            if (left < 0) {
              this.data.left = 0
            } else if (
              Math.round(
                (left * this.$store.state.size.w) / 100 +
                  Math.round((this.width * this.data.zoom) / 100)
              ) > this.$store.state.size.w
            ) {
              this.data.left =
                ((this.$store.state.size.w -
                  Math.round((this.width * this.data.zoom) / 100)) *
                  100) /
                this.$store.state.size.w
            } else {
              this.data.left = left
            }
          }
        }

        const up = () => {
          document.removeEventListener('mousemove', move)
          document.removeEventListener('mouseup', up)
        }
        document.addEventListener('mousemove', move)
        document.addEventListener('mouseup', up)
      }
    },
    keyboard(event) {
      event.preventDefault()
      event.stopPropagation()
      if (event.keyCode === 13) {
        // Enter
      } else if (event.keyCode === 37) {
        // 左
        this.data.left--
      } else if (event.keyCode === 38) {
        // 上
        this.data.top--
      } else if (event.keyCode === 39) {
        // 右
        this.data.left++
      } else if (event.keyCode === 40) {
        // 下
        this.data.top++
      }
      this.selfAdapation()
    },
    selfAdapation() {
      this.$nextTick(() => {
        this.width = this.$refs.bar.clientWidth
        this.height = this.$refs.bar.clientHeight

        const top = this.data.top
        if (top < 0) {
          this.data.top = 0
        } else if (
          Math.round(
            (top * this.$store.state.size.h) / 100 +
              Math.round((this.height * this.data.zoom) / 100)
          ) > this.$store.state.size.h
        ) {
          this.data.top =
            ((this.$store.state.size.h -
              Math.round((this.height * this.data.zoom) / 100)) *
              100) /
            this.$store.state.size.h
        } else {
          this.data.top = top
        }
        //  宽
        const left = this.data.left
        if (left < 0) {
          this.data.left = 0
        } else if (
          Math.round(
            (left * this.$store.state.size.w) / 100 +
              Math.round((this.width * this.data.zoom) / 100)
          ) > this.$store.state.size.w
        ) {
          this.data.left =
            ((this.$store.state.size.w -
              Math.round((this.width * this.data.zoom) / 100)) *
              100) /
            this.$store.state.size.w
        } else {
          this.data.left = left
        }
      })
    }
  }
}
</script>

