<template>
  <div class="pvw" :style="{ width: width + 'px', height: height + 'px' }">
    <div class="pvw-canvas">
      <ImgEle
        v-for="item in filterOfImg"
        :key="item.id"
        :isShow="Number(item.pvw)"
        :data="item.data"
        :updateTime="item.updateTime"
      ></ImgEle>
      <TextEle
        v-for="item in filterOfText"
        :key="item.id"
        :id="item.id"
        :isShow="Number(item.pvw)"
        :data="item.data"
        :updateTime="item.updateTime"
        :resolution="$store.state.size.name"
      ></TextEle>
      <BarEle
        v-for="item in filterOfBar"
        :key="item.id"
        :isShow="Number(item.pvw)"
        :data="item.data"
        :updateTime="item.updateTime"
      ></BarEle>
      <ScoreEle
        v-for="item in filterOfScore"
        :key="item.id"
        :isShow="Number(item.pvw)"
        :data="item.data"
        :updateTime="item.updateTime"
      ></ScoreEle>
      <TimeEle
        v-for="item in filterOfTime"
        :key="item.id"
        :isShow="Number(item.pvw)"
        :data="item.data"
        :updateTime="item.updateTime"
      ></TimeEle>
    </div>
  </div>
</template>

<script>
import API from '@/api'
import qs from 'qs'

export default {
  name: 'Pvw',
  data() {
    return {
      width: 0,
      height: 0,
      outputResolution: '',
      layoutList: [],
      clientId: '',
      dmsTimer: null
    }
  },
  created() {
    this.clientId =
      '_' + String(new Date().getTime() + Math.floor(Math.random() * 10000 + 1))
  },
  mounted() {
    this.width = this.$store.state.size.w
    this.height = this.$store.state.size.h
    this.initDmsService()
    this.getSceneList()
  },
  beforeDestroy() {
    clearInterval(this.dmsTimer)
    this.dmsTimer = null
    /* eslint-disable no-undef */
    ROP.Leave()
    /* eslint-disable no-undef */
  },
  computed: {
    filterOfImg() {
      const arr = []
      this.layoutList.forEach(element => {
        if (element.type === 'img') arr.push(element)
      })
      console.log(arr)
      return arr
    },
    filterOfText() {
      const arr = []
      this.layoutList.forEach(element => {
        if (element.type === 'text') arr.push(element)
      })
      console.log(arr)
      return arr
    },
    filterOfBar() {
      const arr = []
      this.layoutList.forEach(element => {
        if (element.type === 'bar') arr.push(element)
      })
      console.log(arr)
      return arr
    },
    filterOfScore() {
      const arr = []
      this.layoutList.forEach(element => {
        if (element.type === 'score') arr.push(element)
      })
      console.log(arr)
      return arr
    },
    filterOfTime() {
      const arr = []
      this.layoutList.forEach(element => {
        if (element.type === 'time') arr.push(element)
      })
      console.log(arr)
      return arr
    }
  },
  methods: {
    initDmsService() {
      const self = this
      /* eslint-disable no-undef */
      if (typeof ROP !== 'undefined') {
        const topic = 'bcc_hsms_pvw_' + self.$store.state.lcpsData.appid
        ROP.Enter('', self.$store.state.dmsInfo.subKey, self.clientId, true)

        ROP.On('enter_suc', () => {
          self.$sentry.captureMessage(
            self.$store.state.lcpsData.appid + '- DMS Enter_suc',
            'info'
          )
          if (self.dmsTimer) clearInterval(self.dmsTimer)
          ROP.Subscribe(topic)
        })
        ROP.On('enter_fail', () => {
          self.$sentry.captureException(
            new Error(self.$store.state.lcpsData.appid + '- DMS Enter_Fail')
          )
          if (self.dmsTimer) clearInterval(self.dmsTimer)
          self.dmsTimer = setInterval(self.initDmsService(), 500)
        })
        ROP.On('publish_data', data => {
          const published_data = JSON.parse(data)
          console.log(published_data)
          self.getSceneList()
        })
        ROP.On('connectold', () => {
          self.$sentry.captureException(
            new Error(
              self.$store.state.lcpsData.appid + '- DMS Connectold Losed'
            )
          )
          if (self.dmsTimer) clearInterval(self.dmsTimer)
          self.dmsTimer = setInterval(self.initDmsService(), 500)
        })
        ROP.On('offline', err => {
          console.log('offline:' + err)
          self.$sentry.captureException(
            self.$store.state.lcpsData.appid + '- DMS Offline',
            'info'
          )
        })
        ROP.On('reconnect', () => {
          console.log('reconnect')
          if (self.dmsTimer) clearInterval(self.dmsTimer)
          self.$sentry.captureMessage(
            self.$store.state.lcpsData.appid + '- DMS Reconnect',
            'info'
          )
        })
        ROP.On('losed', () => {
          self.$sentry.captureException(
            new Error(self.$store.state.lcpsData.appid + '- DMS Losed')
          )
          if (self.dmsTimer) clearInterval(self.dmsTimer)
          self.dmsTimer = setInterval(self.initDmsService(), 500)
        })
        /* eslint-disable no-undef */
      } else {
        this.initDmsService()
      }
    },
    getSceneList() {
      this.$axios({
        method: 'post',
        url: API.getPvwSceneList,
        data: qs.stringify({
          appId: this.$store.state.lcpsData.appid,
          uin: this.$store.state.lcpsData.uin
        }),
        headers: {
          // "X-Ca-Stage": "PRE",
          // token: this.$cookies.get("CONSOLE_TOKEN_GDY"),
          'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
        }
      })
        .then(res => {
          if (
            res &&
            Number(res.data.code) === 200 &&
            Number(res.data.errorCode) === 0
          ) {
            this.$store.dispatch('refreshData', {
              name: 'pvwLayoutList',
              value: res.data.data
            })
          } else {
            this.$sentry.setExtra('response', { res: res })
            this.$sentry.captureException(
              new Error('场景列表获取失败:' + res.data.errorMessage)
            )
          }
        })
        .catch(err => {
          console.log(err.response)
          this.$sentry.setExtra('response', { err: err.response })
          this.$sentry.captureException(new Error('场景列表获取异常:' + err))
        })
    }
  },
  watch: {
    '$store.state.pvwLayoutList': {
      handler(nv, ov) {
        this.layoutList = [...nv]
        console.log('Refresh', this.layoutList)
      },
      deep: true,
      immediate: true
    }
    // "$store.state.size": {
    //   handler(nv, ov) {
    //     this.width = nv.w;
    //     this.height = nv.h;
    //   },
    //   deep: true,
    //   immediate: true
    // }
  }
}
</script>

<style lang="scss" scoped>
.pvw {
  width: 100%;
  height: 100%;
  background-color: transparent;
  border: none;

  .pvw-canvas {
    position: relative;
    width: 100%;
    height: 100%;
    background-color: transparent;
    border: none;
    transform-origin: 0 0;
    -moz-transform-origin: 0 0;
    -webkit-transform-origin: 0 0;
    -o-transform-origin: 0 0;
    z-index: 0;
    overflow: hidden;
  }
}
</style>
