<template>
  <div
    class="time-ele"
    :class="{ selected: selected }"
    draggable="false"
    v-if="isShow == 1"
    :style="styleBasic"
    :tabindex="1"
  >
    <section ref="content" class="content">
      <div>
        <span ref="time" class="time" :style="styleText">{{ timeString }}</span>
      </div>
    </section>
  </div>
</template>

<script>
import dayjs from 'dayjs'

export default {
  name: 'TimeEle',
  props: {
    id: Number,
    isShow: Number,
    data: Object,
    updateTime: Number,
    selected: {
      type: Boolean,
      default: false
    },
    removable: {
      type: Boolean,
      default: false
    },
    allowedOverStep: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      timer: null,
      timeString: '',
      format_data: 'YYYY-MM-DD dddd HH:mm:ss',
      width: 0,
      height: 0,
      startX: 0,
      startY: 0,
      startTop: 0,
      startLeft: 0
    }
  },
  created() {
    switch (this.data.style) {
      case 1:
        this.format_data = 'HH:mm:ss' // 小时-分钟
        break
      case 2:
        this.format_data = 'YYYY-MM-DD' // 年-月-日
        break
      case 3:
        this.format_data = 'YYYY-MM-DD HH:mm:ss' // 年-月-日
        break
      default:
        break
    }
    if (this.timer) clearInterval(this.timer)
    this.timeString = dayjs().format(this.format_data)
    this.timer = setInterval(() => {
      this.timeString = dayjs().format(this.format_data)
    }, 1000)
    console.log('Time:', dayjs().format(this.format_data))
  },
  beforeDestroy() {
    clearInterval(this.timer)
  },
  computed: {
    styleBasic() {
      return {
        top: this.data.top + '%',
        left: this.data.left + '%',
        zIndex: this.updateTime
      }
    },
    styleText() {
      return {
        fontSize: this.data.size + 'px',
        fontWeight: '500',
        color: this.data.color
      }
    },
    dateString() {
      return () => {
        return dayjs().format(this.format_data)
      }
    }
  },
  methods: {
    mouseDown(event) {
      let coefficient = 0.3125
      if (this.$store.state.size.name === '1080p') coefficient = 0.20833333
      if (this.removable) {
        this.$nextTick(() => {
          this.width = this.$refs.text.clientWidth
          this.height = this.$refs.text.clientHeight
        })
        this.startY = event.clientY
        this.startX = event.clientX
        this.startLeft = Math.round(
          (Number(this.data.left) * this.$store.state.size.w) / 100
        )
        this.startTop = Math.round(
          (Number(this.data.top) * this.$store.state.size.h) / 100
        )
        console.log(this.startX, this.startY, this.startTop, this.startLeft)

        const move = moveEvent => {
          const currX = moveEvent.clientX
          const currY = moveEvent.clientY
          if (this.allowedOverStep) {
            this.data.top = Math.round(
              ((currY - this.startY + this.startTop) * 100) /
                this.$store.state.size.h
            )
            this.data.left = Math.round(
              ((currX - this.startX + this.startLeft) * 100) /
                this.$store.state.size.w
            )
          } else {
            //  高
            const top = Math.round(
              (((currY - this.startY) / coefficient + this.startTop) * 100) /
                this.$store.state.size.h
            )
            if (top < 0) {
              this.data.top = 0
            } else if (
              Math.round(
                (top * this.$store.state.size.h) / 100 +
                  Math.round((this.height * this.data.zoom) / 100)
              ) > this.$store.state.size.h
            ) {
              this.data.top =
                ((this.$store.state.size.h -
                  Math.round((this.height * this.data.zoom) / 100)) *
                  100) /
                this.$store.state.size.h
            } else {
              this.data.top = top
            }
            //  宽
            const left = Math.round(
              (((currX - this.startX) / coefficient + this.startLeft) * 100) /
                this.$store.state.size.w
            )
            if (left < 0) {
              this.data.left = 0
            } else if (
              Math.round(
                (left * this.$store.state.size.w) / 100 +
                  Math.round((this.width * this.data.zoom) / 100)
              ) > this.$store.state.size.w
            ) {
              this.data.left =
                ((this.$store.state.size.w -
                  Math.round((this.width * this.data.zoom) / 100)) *
                  100) /
                this.$store.state.size.w
            } else {
              this.data.left = left
            }
          }
        }

        const up = () => {
          document.removeEventListener('mousemove', move)
          document.removeEventListener('mouseup', up)
        }
        document.addEventListener('mousemove', move)
        document.addEventListener('mouseup', up)
      }
    },
    keyboard(event) {
      event.preventDefault()
      event.stopPropagation()
      if (event.keyCode === 13) {
        // Enter
      } else if (event.keyCode === 37) {
        // 左
        this.data.left--
      } else if (event.keyCode === 38) {
        // 上
        this.data.top--
      } else if (event.keyCode === 39) {
        // 右
        this.data.left++
      } else if (event.keyCode === 40) {
        // 下
        this.data.top++
      }
    },
    selfAdapation() {
      this.$nextTick(() => {
        this.width = this.$refs.text.clientWidth
        this.height = this.$refs.text.clientHeight

        if (
          Math.round((this.width * this.data.zoom) / 100) >
          this.$store.state.size.w
        ) {
          this.data.zoom = Math.round(
            (this.$store.state.size.w * 100) / this.width
          )
        }

        const top = this.data.top
        if (top < 0) {
          this.data.top = 0
        } else if (
          Math.round(
            (top * this.$store.state.size.h) / 100 +
              Math.round((this.height * this.data.zoom) / 100)
          ) > this.$store.state.size.h
        ) {
          this.data.top =
            ((this.$store.state.size.h -
              Math.round((this.height * this.data.zoom) / 100)) *
              100) /
            this.$store.state.size.h
        } else {
          this.data.top = top
        }
        //  宽
        const left = this.data.left
        if (left < 0) {
          this.data.left = 0
        } else if (
          Math.round(
            (left * this.$store.state.size.w) / 100 +
              Math.round((this.width * this.data.zoom) / 100)
          ) > this.$store.state.size.w
        ) {
          this.data.left =
            ((this.$store.state.size.w -
              Math.round((this.width * this.data.zoom) / 100)) *
              100) /
            this.$store.state.size.w
        } else {
          this.data.left = left
        }
      })
    }
  },
  watch: {
    'data.style': {
      handler(nv, ov) {
        if (nv !== ov) {
          switch (nv) {
            case 1:
              this.format_data = 'HH:mm:ss' // 小时-分钟
              break
            case 2:
              this.format_data = 'YYYY-MM-DD' // 年-月-日
              break
            case 3:
              this.format_data = 'YYYY-MM-DD HH:mm:ss' // 年-月-日
              break
            default:
              break
          }
          if (this.timer) clearInterval(this.timer)
          this.timeString = dayjs().format(this.format_data)
          this.timer = setInterval(() => {
            this.timeString = dayjs().format(this.format_data)
          }, 1000)
        }
      },
      deep: true,
      immediate: true
    }
  }
}
</script>

<style lang="scss" scoped>
.selected {
  border: 2px dashed #26d2c9 !important;
  cursor: move !important;
}

.time-ele {
  // font-family: 'SourceHanSansCN';
  position: absolute;

  &:focus {
    outline: none;
  }

  section {
    white-space: nowrap;

    .time {
      position: absolute;
      text-align: center;
      vertical-align: middle;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
}
</style>
