<template>
  <div
    class="text-ele"
    :class="{ selected: selected }"
    draggable="false"
    v-if="isShow == 1"
    :style="styleBasic"
    :tabindex="1"
  >
    <section ref="content" class="content">
      <div>
        <span ref="text" class="text" :style="styleText">{{ data.cont }}</span>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  name: 'TextEle',
  props: {
    id: Number,
    isShow: Number,
    data: Object,
    updateTime: Number,
    selected: {
      type: Boolean,
      default: false
    },
    removable: {
      type: Boolean,
      default: false
    },
    allowedOverStep: {
      type: Boolean,
      default: false
    },
    resolution: String,
    fixed: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      timer: null,
      width: 0,
      height: 0,
      startX: 0,
      startY: 0,
      startTop: 0,
      startLeft: 0
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.width = this.$refs.text.clientWidth
      if (this.resolution === '720p') {
        this.addAnimation(this.id, 1280, this.width)
      } else if (this.resolution === '1080p') {
        this.addAnimation(this.id, 1920, this.width)
      } else {
        console.log('No resolution')
      }
    })
  },
  computed: {
    styleBasic() {
      if (this.data.show === 'no' || this.fixed) {
        return {
          top: this.data.top + '%',
          left: this.data.left + '%',
          zIndex: this.updateTime
        }
      } else {
        return {
          top: this.data.top + '%',
          left: '100%',
          zIndex: this.updateTime
        }
      }
    },
    styleText() {
      let loop
      const duration =
        this.width > this.$store.state.size.w
          ? Math.round(
              (this.data.speed * this.width) / (1000 * this.$store.state.size.w)
            )
          : Math.round(this.data.speed / 1000)
      if (this.data.show === 'no') {
        // do nothing
        return {
          fontSize: this.data.size + 'px',
          fontWeight: 'bold',
          color: this.data.color
        }
      } else if (this.data.show === 'one') {
        // once
        loop = false
      } else if (this.data.show === 'loop') {
        // loop
        loop = true
      }
      return {
        fontSize: this.data.size + 'px',
        fontWeight: 'bold',
        color: this.data.color,
        animationName: 'text_marquee_' + this.id,
        animationTimingFunction: 'linear',
        animationDirection: 'normal',
        animationFillMode: 'none',
        animationIterationCount: loop ? 'infinite' : 'initial',
        animationDuration: duration + 's'
      }
    }
  },
  methods: {
    addAnimation(id, width, textWidth) {
      const dsitance = width + textWidth
      this.addStylesheetRules([
        `@keyframes text_marquee_` +
          id +
          ` {
            0% {
              transform: translateX(0px);
              -webkit-transform: translateX(0px);
            }
            100% {
              transform: translateX(-` +
          dsitance +
          `px);
              -webkit-transform: translateX(-` +
          dsitance +
          `px);
            }
          }`
      ])
    },
    addStylesheetRules(rules) {
      const styleEl = document.createElement('style')
      document.head.appendChild(styleEl)

      // 获取样式
      const styleSheet = styleEl.sheet
      // 插入样式
      for (let i = 0; i < rules.length; i++) {
        styleSheet.insertRule(rules[i], styleSheet.cssRules.length) // 后面一个参数表示插入位置的索引
      }
    },
    mouseDown(event) {
      let coefficient = 0.3125
      if (this.$store.state.size.name === '1080p') coefficient = 0.20833333
      if (this.removable) {
        this.$nextTick(() => {
          this.width = this.$refs.text.clientWidth
          this.height = this.$refs.text.clientHeight
        })
        this.startY = event.clientY
        this.startX = event.clientX
        this.startLeft = Math.round(
          (Number(this.data.left) * this.$store.state.size.w) / 100
        )
        this.startTop = Math.round(
          (Number(this.data.top) * this.$store.state.size.h) / 100
        )
        console.log(this.startX, this.startY, this.startTop, this.startLeft)

        const move = moveEvent => {
          const currX = moveEvent.clientX
          const currY = moveEvent.clientY
          if (this.allowedOverStep) {
            this.data.top = Math.round(
              ((currY - this.startY + this.startTop) * 100) /
                this.$store.state.size.h
            )
            this.data.left = Math.round(
              ((currX - this.startX + this.startLeft) * 100) /
                this.$store.state.size.w
            )
          } else {
            //  高
            const top = Math.round(
              (((currY - this.startY) / coefficient + this.startTop) * 100) /
                this.$store.state.size.h
            )
            if (top < 0) {
              this.data.top = 0
            } else if (
              Math.round(
                (top * this.$store.state.size.h) / 100 +
                  Math.round((this.height * this.data.zoom) / 100)
              ) > this.$store.state.size.h
            ) {
              this.data.top =
                ((this.$store.state.size.h -
                  Math.round((this.height * this.data.zoom) / 100)) *
                  100) /
                this.$store.state.size.h
            } else {
              this.data.top = top
            }
            //  宽
            const left = Math.round(
              (((currX - this.startX) / coefficient + this.startLeft) * 100) /
                this.$store.state.size.w
            )
            if (left < 0) {
              this.data.left = 0
            } else if (
              Math.round(
                (left * this.$store.state.size.w) / 100 +
                  Math.round((this.width * this.data.zoom) / 100)
              ) > this.$store.state.size.w
            ) {
              this.data.left =
                ((this.$store.state.size.w -
                  Math.round((this.width * this.data.zoom) / 100)) *
                  100) /
                this.$store.state.size.w
            } else {
              this.data.left = left
            }
          }
        }

        const up = () => {
          document.removeEventListener('mousemove', move)
          document.removeEventListener('mouseup', up)
        }
        document.addEventListener('mousemove', move)
        document.addEventListener('mouseup', up)
      }
    },
    keyboard(event) {
      event.preventDefault()
      event.stopPropagation()
      if (event.keyCode === 13) {
        // Enter
      } else if (event.keyCode === 37) {
        // 左
        this.data.left--
      } else if (event.keyCode === 38) {
        // 上
        this.data.top--
      } else if (event.keyCode === 39) {
        // 右
        this.data.left++
      } else if (event.keyCode === 40) {
        // 下
        this.data.top++
      }
    },
    selfAdapation() {
      this.$nextTick(() => {
        this.width = this.$refs.text.clientWidth
        this.height = this.$refs.text.clientHeight

        if (
          Math.round((this.width * this.data.zoom) / 100) >
          this.$store.state.size.w
        ) {
          this.data.zoom = Math.round(
            (this.$store.state.size.w * 100) / this.width
          )
        }

        const top = this.data.top
        if (top < 0) {
          this.data.top = 0
        } else if (
          Math.round(
            (top * this.$store.state.size.h) / 100 +
              Math.round((this.height * this.data.zoom) / 100)
          ) > this.$store.state.size.h
        ) {
          this.data.top =
            ((this.$store.state.size.h -
              Math.round((this.height * this.data.zoom) / 100)) *
              100) /
            this.$store.state.size.h
        } else {
          this.data.top = top
        }
        //  宽
        const left = this.data.left
        if (left < 0) {
          this.data.left = 0
        } else if (
          Math.round(
            (left * this.$store.state.size.w) / 100 +
              Math.round((this.width * this.data.zoom) / 100)
          ) > this.$store.state.size.w
        ) {
          this.data.left =
            ((this.$store.state.size.w -
              Math.round((this.width * this.data.zoom) / 100)) *
              100) /
            this.$store.state.size.w
        } else {
          this.data.left = left
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.normalLength {
  width: 1280px;
}

.extendLength {
  width: 1920px;
}

.selected {
  border: 2px dashed #26d2c9 !important;
  cursor: move !important;
}

.text-ele {
  // font-family: 'SourceHanSansCN';
  position: absolute;

  &:focus {
    outline: none;
  }

  section {
    white-space: nowrap;

    .text {
      position: absolute;
      text-align: center;
      vertical-align: middle;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
}
</style>
