import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    dmsInfo: {},
    layoutList: [],
    pvwLayoutList: [],
    size: {
      name: '720p',
      w: 1280,
      h: 720
    },
    lcpsData: {} // appid; uin; env;
  },
  getters: {
    getData: state => {
      return state
    }
  },
  mutations: {
    refreshData(state, obj) {
      state[obj.name] = obj.value
    }
  },
  actions: {
    refreshData({ commit }, obj) {
      commit('refreshData', obj)
    }
  }
})
