import Vue from 'vue'
import App from './App.vue'
import router from './router/router'
import store from './store/store'
import axios from 'axios'
import VueCookies from 'vue-cookies'
import Vcomp from './components/index'
import * as Sentry from '@sentry/browser'
import * as Integrations from '@sentry/integrations'
import utils from './utils'
import API from './api'
import pConfig from '../package.json'

import '@/assets/fonts/font.css'
import './assets/styles/global.scss'

Vue.config.productionTip = false

// Vcomp
Vue.use(Vcomp)
// VueCookies
Vue.use(VueCookies)
// Sentry
Sentry.init({
  dsn: 'https://9ca134d9aa3b4ed5ab471bd34ee808d0@sentry.io/1533867',
  integrations: [
    new Integrations.Vue({
      Vue,
      attachProps: true
    })
  ],
  release: pConfig.version,
  environment: process.env.VUE_APP_RUN_ENV
})

Vue.prototype.$axios = axios
Vue.prototype.$utils = utils
Vue.prototype.$sentry = Sentry
Vue.prototype.$cookies = VueCookies

router.beforeEach((to, from, next) => {
  // https://consoleapi.guangdianyun.tv/v1/account/person/getDmsKeyInfo
  const appid = String(utils.parseUrl('appId'))
  const uin = String(utils.parseUrl('uin'))
  let env = ''

  if (process.env.VUE_APP_RUN_ENV === 'development') {
    env = 'TEST'
  } else if (process.env.VUE_APP_RUN_ENV === 'preview') {
    env = 'PRE'
  } else if (process.env.VUE_APP_RUN_ENV === 'production') {
    env = ''
  }

  const lcpsData = {
    appid: appid,
    uin: uin,
    env: env
  }

  store.dispatch('refreshData', {
    name: 'lcpsData',
    value: lcpsData
  })

  axios({
    method: 'get',
    url: API.getDmsInfo + uin,
    headers: {
      'X-Ca-Stage': env,
      // token: token,
      'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
    }
  })
    .then(res => {
      console.log(res.data)
      if (
        res &&
        Number(res.data.code) === 200 &&
        Number(res.data.errorCode) === 0
      ) {
        store.dispatch('refreshData', {
          name: 'dmsInfo',
          value: res.data.data
        })
        axios({
          method: 'get',
          url: API.getAppInfo + appid,
          headers: {
            'X-Ca-Stage': env,
            // token: this.$cookies.get("CONSOLE_TOKEN_GDY"),
            'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
          }
        })
          .then(r => {
            console.log(r)
            if (
              r &&
              Number(r.data.code) === 200 &&
              Number(r.data.errorCode) === 0
            ) {
              let width = 0
              let height = 0
              if (r.data.data.outputResolution === '720p') {
                width = 1280
                height = 720
              } else if (r.data.data.outputResolution === '1080p') {
                width = 1920
                height = 1080
              } else {
                // width = 1280;
                // height = 720;
              }
              store.dispatch('refreshData', {
                name: 'size',
                value: {
                  name: r.data.data.outputResolution,
                  w: width,
                  h: height
                }
              })
              return next()
            } else {
              console.log(r.data.errorMessage)
              Sentry.setExtra('response', { res: r })
              Sentry.captureException(
                new Error('导播台信息获取失败:' + r.data.errorMessage)
              )
            }
          })
          .catch(err => {
            console.log(err.response)
            Sentry.setExtra('response', { err: err.response })
            Sentry.captureException(new Error('导播台信息获取异常:' + err))
          })
      } else {
        console.log(res.data.errorMessage)
        Sentry.setExtra('response', { res: res })
        Sentry.captureException(
          new Error('DMS 信息获取失败:' + res.data.errorMessage)
        )
      }
    })
    .catch(err => {
      console.log('Get Dms Error')
      Sentry.setExtra('response', { err: err.response })
      Sentry.captureException(new Error('DMS 信息获取异常:' + err))
    })
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
